
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #222324;
  color: #fafcfe;
}

.hljs-comment
 {
  color: #57A64A;
 }

.hljs-number
{
  color: #b5cea8;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-built_in,
.hljs-name,
.hljs-tag {
  color: #569cd6;
}

.hljs-literal
{
  color:#569cd6 ;
}
.hljs-title
{
  color:#4EC9B0;
}

.hljs-string,
.hljs-section,
.hljs-attribute,
.hljs-template-tag,
.hljs-template-variable,
.hljs-type,
.hljs-addition {
  color: #ce9178;
}

.hljs-deletion,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-meta {
  color: #2b91af;
}

.hljs-doctag {
  color: #808080;
}

.hljs-attr {
  color: #9cdcfe;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link {
  color: #808080;
}


.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

